import React from 'react';
import {connect} from 'react-redux';
import Button from './components/button';


import './index.css';

class SubscriptionItem extends React.Component {
    constructor(props) {
        super(props);
        this.props.Item= JSON.parse(this.props.subscriptionItem);
        this.state = {isToggleOn: false};
        this.state.percentageOfUse = ((this.props.Item.billableLicenses / this.props.Item.vendorLicenses) * 100) + "%";
        this.state.color = ((this.props.Item.billableLicenses / this.props.Item.vendorLicenses) * 100) < 35 ? "red" : "#51b6b5";
        this.state.status = this.props.Item.vendorStatus === "EVAL" ? "#ff8411" : " #289728";

        if(this.props.Item.catalog.unit !== "pack"){
            this.state.totalPrice = (this.props.Item.vendorLicenses * this.props.Item.catalog.unitCost);
        }
        else{
            this.state.totalPrice = this.props.Item.catalog.unitCost;
        }


        // This binding is necessary to make `this` work in the callback
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn
        }));
    }

    render() {

        const formatDate = (dateToFormat) => {
            let options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
            return new Date(dateToFormat).toLocaleDateString('en-EN', options);
        };

        return (
            <div className="product-card whitebox nopad rounded">
                <div className="product-card-header">
                    <div className="row">
                        <div className="col col-lg-12 product-card-header-content">
                            <div className="product-logo row">
                                <div className="col col-lg-6 row">
                                    <div className="col col-lg-3">
                                        <p className="GscPicto">
                                            {this.props.Item.product.name === 'G Suite' && <img src="https://www.econsulting.fr/sm/g-suite.png" alt="SubscriptionImage" />}
                                            {this.props.Item.product.name === 'Google Vault' && <img src="https://www.econsulting.fr/sm/vault.png" alt="SubscriptionImage" />}
                                            {this.props.Item.product.name === 'Google Drive' && <img src="https://www.econsulting.fr/sm/drive.png" alt="SubscriptionImage" />}
                                            {this.props.Item.product.name === 'Shared Contacts for Gmail' && <img src="https://www.econsulting.fr/sm/shared-contact-for-gmail-pic.png" alt="SubscriptionImage" />}
                                            {this.props.Item.product.name === 'Shared Contacts for G Suite' && <img src="https://www.econsulting.fr/sm/shared-contact-for-gmail-pic.png" alt="SubscriptionImage" />}
                                        </p>
                                    </div>
                                    <div className="col col-lg-7">
                                        <p className="Product">{this.props.Item.product.name}</p>
                                        <p className="ProductVersion">{this.props.Item.product.version}</p>
                                        <p className="domainname">{this.props.Item.domain}</p>
                                        <div className="statut">
                                            <b className="cercle" style={{backgroundColor: this.state.status }}>&nbsp;</b>
                                            <span value="EVAL">{this.props.Item.vendorStatus}</span>
                                        </div>
                                    </div>
                                </div>
                                <div className="col col-lg-6 row">
                                    <div className="col col-lg-4 licences">
                                        <p className="uppercase">Licences &nbsp; | &nbsp; Domain users</p>
                                        <p className="nbrLicences">
                                            <strong>
                                                <span className="spanLicence bold">{this.props.Item.billableLicenses}</span> &nbsp; | &nbsp;
                                            </strong>
                                            <span className="spanUser">{this.props.Item.vendorLicenses}</span>
                                        </p>
                                        <div className="progress">
                                            <div className="determinate" style={{width: this.state.percentageOfUse, backgroundColor: this.state.color }}>&nbsp;</div>
                                        </div>
                                    </div>
                                    <div className="col col-lg-4 licences">
                                        <p>TOTAL</p>
                                        <p className="price">
                                        <span className="price-amount">{this.props.Item.currency === 'USD' ? '$' : ''}{this.state.totalPrice}{this.props.Item.currency !== 'USD' ? '€' : ''}</span><br/>
                                        </p>
                                        <div className="frequencyHeader">
                                            PER {this.props.Item.product.plan === 'ANNUAL_YEARLY' ? 'YEAR' : 'MONTH'}
                                        </div>
                                    </div>
                                    <div className=" col col-lg-4 actionSection">
                                        <div className="btn-wrapper col col-lg-12">
                                            <Button status={this.props.Item.vendorStatus} renew={this.props.Item.renewalOption} frequency={this.props.Item.product.plan}/>
                                        </div>
                                        <div className="btn-wrapper col col-lg-12">
                                            <a href="http://staging-app.gmailsharedcontacts.com"><button className="launchButton uppercase">Launch</button></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col col-lg-12 row">
                            <div className="col col-lg-6 addons"></div>
                            <div className="col col-lg-6 row">
                                <div className="col col-lg-4 push-l1"></div>
                                <div className="col col-lg-4 push-l1 promotions">{this.props.Item.promotionBanner}</div>
                                <div className="col col-lg-3 push-l2"></div>
                            </div>
                        </div>
                    </div>
                    <div onClick={this.handleClick} className="item-addons" >
                        {this.state.isToggleOn ?
                            <i className="fa fa-chevron-up" aria-hidden="true"></i>
                            :
                            <i className="fa fa-chevron-down" aria-hidden="true"></i>
                        }
                    </div>
                    <div className={"item-contract-details row " + (this.state.isToggleOn ? 'activeBottom' : '')} ref="itemContractDetails">
                        <div className="col col-lg-3">
                        <p><strong className="col col-lg-6">Unit cost</strong><span>{this.props.Item.currency === 'USD' ? '$' : ""}{this.props.Item.catalog.unitCost}{this.props.Item.currency !== 'USD' ? '€' : ""}/{this.props.Item.catalog.unit}/{this.props.Item.product.plan === 'ANNUAL_YEARLY' ? 'year' : 'month'}</span></p>
                        <p><strong className="col col-lg-6">Started on</strong><span>{formatDate(this.props.Item.startPlanDate)}</span></p>
                        <p><strong className="col col-lg-6">{this.props.Item.renewalOption === "CANCEL" ? "Commitment ends" : "Commitment renews"}</strong><span>{formatDate(this.props.Item.expiryDate)}</span></p>
                        <p><strong className="col col-lg-6">Renewal Option</strong><span>{this.props.Item.renewalOption === "CANCEL" ? "Cancel" : "Renew"}</span></p>
                        </div>
                        <div className="col col-lg-4">
                        <p><strong className="col col-lg-6">Payment method</strong>
                            {this.props.Item.paymentMethod.type === 'Credit Card' && <span>Credit card ending with {this.props.Item.paymentMethod.last_4_digit}</span>}
                            {this.props.Item.paymentMethod.type === 'Paypal' && <span>Paypal account {this.props.Item.paymentMethod.emailAddress}</span>}
                        </p>
                        <p><strong className="col col-lg-6">Billing every</strong><span>{this.props.Item.product.plan === 'ANNUAL_YEARLY' ? 'Year' : 'Month'}</span></p>
                        <p><strong className="col col-lg-6">Next invoice</strong><span>{formatDate(this.props.Item.nextInvoiceDate)}</span></p>
                        </div>
                        <div className="col col-lg-12"></div>
                    </div>
                </div>
                <div className="product-card-body">
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {

    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        test : (number) => dispatch({ type: 'INCREMENT', payload: number }),
        test2 : (name) => dispatch({ type: 'SET_NAME', payload: name })
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubscriptionItem);







