const subscriptionReducer = (state={counter:0, lastValues: []}, action) => {
    switch (action.type){
        case "INCREMENT":
            state = {
                ...state,
                counter: state.counter + action.payload,
                lastValues: [state.counter , ...state.lastValues]
            };
            break;
        default: return state;
    }
    return state;
};

export default subscriptionReducer;


