import React from 'react';
import './index.css';

export default class Assistance extends React.Component {

    render() {
        return (
            <div className="assistance">
                <div>
                    <h2>Need assistance?</h2>
                    <p>
                        If you need help with your subscription please contact our support team at <a href="mailto:support@gmailsharedcontacts.com">support@gmailsharedcontacts.com</a>.
                    </p>
                </div>
            </div>
        );
    }
}


