const userReducer = (state={name: null, lastValues: []}, action) => {
    switch (action.type){
        case "SET_NAME":
            state = {
                ...state,
                name: action.payload,
                lastValues: [state.name , ...state.lastValues]
            };
            break;
        default: return state;
    }
    return state;
};

export default userReducer;


